export default {
	data() {
		return {
			zhanghao:'',
			xingming:'',
			shouji:'',
			mima:'',
		}
	},
	onLoad:function () {
		// this.creatUser();
	},
	methods: {
		creatUser()
		{
			// if(!this.zhanghao)
			// {
			// 	uni.showToast({
			// 		title: '账号不能为空!',
			// 		duration: 2000,
			// 		icon:'none'
			// 	});
			// 	return;
			// }
			if(!this.xingming)
			{
				uni.showToast({
					title: '昵称不能为空!',
					duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.mima)
			{
				uni.showToast({
					title: '密码不能为空!',
					duration: 2000,
					icon:'none'
				});
				return;
			}
			// if(!this.zhanghao)
			// {
			// 	uni.showToast({
			// 		title: '账号不能为空!',
			// 		duration: 2000,
			// 		icon:'none'
			// 	});
			// 	return;
			// }
			this.bus.request({
				url: this.bus.url+'/agent/memberAdd',//?agentId=1
				method: 'GET',
				data: {
					agentId:this.bus.user.userId,
					// agentId:1,
					agentName:this.zhanghao,
					mobile:this.shouji,
					nickName:this.xingming,
					password:this.mima,
					},
				success: res => {
					res = res.data;
					if(res.status=='1')
					{
						// uni.showToast({
						// 	title: '密码修改成功!',
						// 	duration: 2000
						// });
						uni.showToast({
							title: '创建成功!',
							duration: 2000,
							icon:'none'
						});
					} else{
						uni.showToast({
							title: '创建失败!',
							duration: 2000,
							icon:'none'
						});
					}
				},
				fail: () => {
					uni.showToast({
						title:'网络错误!',
						duration: 2000,
						icon:'none'
					});
				},
				complete: () => {
					uni.hideLoading()
				}
			});
		}
	},
}
